.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
}

.friendMessage:not(:last-child) {
    margin-bottom: 30px;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
    gap: 12.5px;
}

.friendImageAndText img {
    height: 48px;
    width: 48px;
}

.friendText {
    background: #F5F7FB;
    padding: 7px 12.5px 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 3px;
    color: #000;
    border-radius: 10px;
}

.friendText:after {

}

.friendName {
    font-weight: 600;
    font-size: 18px;
}

.friendMessageText {
    font-size: 16px;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    line-height: 12.19px;
    margin-right: 11px;
}
