.sendForm {
    margin-top: 56px;
    display: flex;
    gap: 44px;
}

.textarea {
    flex: 1 1 auto;
    resize: none;
    overflow: hidden;
    border-radius: 20px;
    height: 40px !important;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    background: #f5f7fb;
    padding: 12px 24px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
}

.button {
    border-radius: 20px;
    padding: 10px 38px;
    background: #06c;
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    transition: all 0.4s ease;
}

.button:hover {
    cursor: pointer;
    background: #55a2ef;
}

.button:active {
    border: 3px solid #000;
}
