.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 5px;
}

.message:not(:last-child) {
    margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    gap: 12.5px;
}

.imageAndText img {
    height: 48px;
    width: 48px;
}

.text {
    background: #0066CC;
    padding: 7px 12.5px 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 3px;
    color: #fff;
    border-radius: 10px;
}

.text:after {

}

.name {
    font-weight: 600;
    font-size: 18px;
}

.messageText {
    font-size: 16px;
}

.time {
    font-size: 10px;
    font-weight: 600;
    line-height: 12.19px;
    margin-right: 11px;
}